.slideshow-container {
  max-width: 800px;
  margin: auto;
  overflow: hidden;
}

.slide {
  display: none;
}

img {
  width: 100%;
  height: auto;
}

.active {
  display: block;
  animation: fade 1s ease-in-out;
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
